// =============================================================================
// PUBLIC API: SMF NEXFLOW-NATIVE METRICS
// =============================================================================
// GET /api/public/smf-nexflow-metrics
//
// Returns aggregated metrics from NexFlow's own traffic (dogfood, scout, external).
// Complements x402scan and Scattering data with internal telemetry.
//
// Query parameters:
//   - facilitatorId: Filter by facilitator (optional)
//   - network: Filter by network (optional)
//   - timeframe: 1h | 6h | 1d | 7d (default: 1d)
//
// Example:
//   curl https://api.nexflowapp.app/api/public/smf-nexflow-metrics?timeframe=1d
//   curl https://api.nexflowapp.app/api/public/smf-nexflow-metrics?facilitatorId=cdp&network=eip155:8453
//
// =============================================================================

import { NextRequest, NextResponse } from 'next/server';
import { createLogger } from '@/lib/logger';
import { 
  getNexflowMetrics, 
  type Timeframe,
  type NexflowMetricsResponse,
} from '@/services/nexflow-metrics-service';

const logger = createLogger({ component: 'PublicSmfNexflowMetrics' });

// Valid timeframes
const VALID_TIMEFRAMES: Timeframe[] = ['1h', '6h', '1d', '7d'];

/**
 * GET /api/public/smf-nexflow-metrics
 * 
 * Returns NexFlow-native metrics aggregated from internal traffic
 */
export async function GET(request: NextRequest): Promise<NextResponse<NexflowMetricsResponse | { error: string }>> {
  try {
    const { searchParams } = new URL(request.url);
    
    // Parse query parameters
    const facilitatorId = searchParams.get('facilitatorId') ?? undefined;
    const network = searchParams.get('network') ?? undefined;
    const timeframeParam = searchParams.get('timeframe') ?? '1d';
    
    // Validate timeframe
    if (!VALID_TIMEFRAMES.includes(timeframeParam as Timeframe)) {
      return NextResponse.json(
        { error: `Invalid timeframe: ${timeframeParam}. Must be one of: ${VALID_TIMEFRAMES.join(', ')}` },
        { status: 400 }
      );
    }
    
    const timeframe = timeframeParam as Timeframe;

    // Get metrics
    const response = await getNexflowMetrics({
      facilitatorId,
      network,
      timeframe,
    });

    // Log request
    logger.info({
      facilitatorId,
      network,
      timeframe,
      metricsCount: response.metrics.length,
      totalInvocations: response.metrics.reduce((sum, m) => sum + m.invocationsNexflow, 0),
      msg: 'Public SMF NexFlow metrics request',
    });

    return NextResponse.json(response, {
      status: 200,
      headers: {
        'Content-Type': 'application/json',
        'Cache-Control': 'public, max-age=60, s-maxage=60', // Cache for 1 minute
        'Access-Control-Allow-Origin': '*',
        'Access-Control-Allow-Methods': 'GET, OPTIONS',
        'Access-Control-Allow-Headers': 'Content-Type',
      },
    });
  } catch (error) {
    const errorMessage = error instanceof Error ? error.message : 'Unknown error';
    
    logger.error({
      error: errorMessage,
      stack: error instanceof Error ? error.stack : undefined,
      msg: 'Public SMF NexFlow metrics error',
    });

    return NextResponse.json(
      { error: errorMessage },
      { status: 500 }
    );
  }
}

/**
 * OPTIONS /api/public/smf-nexflow-metrics
 * 
 * CORS preflight handler
 */
export async function OPTIONS(): Promise<NextResponse> {
  return new NextResponse(null, {
    status: 204,
    headers: {
      'Access-Control-Allow-Origin': '*',
      'Access-Control-Allow-Methods': 'GET, OPTIONS',
      'Access-Control-Allow-Headers': 'Content-Type',
      'Access-Control-Max-Age': '86400',
    },
  });
}

