// =============================================================================
// NEXFLOW-NATIVE METRICS SERVICE
// =============================================================================
// Service for aggregating NexFlow's own traffic metrics from route logs
// Complements x402scan and Scattering data with internal telemetry
//
// Metrics tracked:
// - Per facilitator, per network
// - Success rate, p95 latency, invocation counts
// - Breakdown by agentType: dogfood, scout, external
//
// =============================================================================

import { createLogger } from '@/lib/logger';
import { Pool } from 'pg';

const logger = createLogger({ component: 'NexflowMetricsService' });

// =============================================================================
// DATABASE CONNECTION
// =============================================================================

let pool: Pool | null = null;

function getPool(): Pool | null {
  if (pool) return pool;
  
  const databaseUrl = process.env.DATABASE_URL;
  if (!databaseUrl || !databaseUrl.startsWith('postgresql://')) {
    return null;
  }
  
  pool = new Pool({
    connectionString: databaseUrl,
    max: 5,
    idleTimeoutMillis: 30000,
    connectionTimeoutMillis: 5000,
  });
  
  return pool;
}

// =============================================================================
// TYPES
// =============================================================================

export type AgentType = 'dogfood' | 'scout' | 'external' | 'unknown';
export type Timeframe = '1h' | '6h' | '1d' | '7d';

export interface AgentTypeMetrics {
  invocations: number;
  successRate: number;
  avgLatencyMs?: number;
}

export interface NexflowFacilitatorMetrics {
  facilitatorId: string;
  network: string;
  successRateNexflow: number;
  p95LatencyNexflow?: number;
  invocationsNexflow: number;
  byAgentType: {
    dogfood?: AgentTypeMetrics;
    scout?: AgentTypeMetrics;
    external?: AgentTypeMetrics;
  };
  lastUpdated: string;
}

export interface NexflowMetricsResponse {
  ok: boolean;
  timeframe: Timeframe;
  metrics: NexflowFacilitatorMetrics[];
  timestamp: string;
}

// =============================================================================
// TIMEFRAME HELPERS
// =============================================================================

function getTimeframeInterval(timeframe: Timeframe): string {
  switch (timeframe) {
    case '1h': return '1 hour';
    case '6h': return '6 hours';
    case '1d': return '1 day';
    case '7d': return '7 days';
    default: return '1 day';
  }
}

// =============================================================================
// QUERY FUNCTIONS
// =============================================================================

/**
 * Get NexFlow-native metrics from route_attempts table
 * Aggregates by facilitator_id, network, and agent_type
 */
export async function getNexflowMetrics(params: {
  facilitatorId?: string;
  network?: string;
  timeframe?: Timeframe;
}): Promise<NexflowMetricsResponse> {
  const timeframe = params.timeframe ?? '1d';
  const interval = getTimeframeInterval(timeframe);
  
  const db = getPool();
  if (!db) {
    logger.warn('Database not available for NexFlow metrics');
    return {
      ok: true,
      timeframe,
      metrics: [],
      timestamp: new Date().toISOString(),
    };
  }

  try {
    // Build query to aggregate route attempts
    // Note: We're reading from smf_route_attempts which records each route attempt
    let query = `
      WITH attempts AS (
        SELECT 
          ra.facilitator_id,
          r.network,
          COALESCE(
            CASE 
              WHEN r.correlation_id LIKE 'agent:%' THEN 
                CASE 
                  WHEN r.correlation_id LIKE '%dogfood%' THEN 'dogfood'
                  WHEN r.correlation_id LIKE '%scout%' THEN 'scout'
                  ELSE 'external'
                END
              ELSE 'external'
            END,
            'unknown'
          ) as agent_type,
          ra.result,
          ra.latency_ms,
          ra.created_at
        FROM smf_route_attempts ra
        JOIN smf_routes r ON ra.route_id = r.id
        WHERE ra.created_at > NOW() - INTERVAL '${interval}'
          AND ra.is_probe = false
    `;
    
    const queryParams: any[] = [];
    let paramIndex = 1;

    if (params.facilitatorId) {
      query += ` AND ra.facilitator_id = $${paramIndex}`;
      queryParams.push(params.facilitatorId);
      paramIndex++;
    }

    if (params.network) {
      query += ` AND r.network = $${paramIndex}`;
      queryParams.push(params.network);
      paramIndex++;
    }

    query += `
      ),
      aggregated AS (
        SELECT 
          facilitator_id,
          network,
          agent_type,
          COUNT(*) as total_attempts,
          SUM(CASE WHEN result = 'success' THEN 1 ELSE 0 END) as success_count,
          AVG(latency_ms) as avg_latency,
          PERCENTILE_CONT(0.95) WITHIN GROUP (ORDER BY latency_ms) as p95_latency,
          MAX(created_at) as last_updated
        FROM attempts
        GROUP BY facilitator_id, network, agent_type
      )
      SELECT 
        facilitator_id,
        network,
        agent_type,
        total_attempts,
        success_count,
        avg_latency,
        p95_latency,
        last_updated
      FROM aggregated
      ORDER BY facilitator_id, network, agent_type
    `;

    const result = await db.query(query, queryParams);

    // Transform into NexflowFacilitatorMetrics format
    const metricsMap = new Map<string, NexflowFacilitatorMetrics>();

    for (const row of result.rows) {
      const key = `${row.facilitator_id}:${row.network}`;
      
      if (!metricsMap.has(key)) {
        metricsMap.set(key, {
          facilitatorId: row.facilitator_id,
          network: row.network,
          successRateNexflow: 0,
          invocationsNexflow: 0,
          byAgentType: {},
          lastUpdated: row.last_updated?.toISOString() ?? new Date().toISOString(),
        });
      }

      const metrics = metricsMap.get(key)!;
      const agentType = row.agent_type as AgentType;
      const totalAttempts = parseInt(row.total_attempts) || 0;
      const successCount = parseInt(row.success_count) || 0;
      const successRate = totalAttempts > 0 ? successCount / totalAttempts : 0;

      // Add to agent type breakdown
      metrics.byAgentType[agentType as keyof typeof metrics.byAgentType] = {
        invocations: totalAttempts,
        successRate: Math.round(successRate * 1000) / 1000,
        avgLatencyMs: row.avg_latency ? Math.round(row.avg_latency) : undefined,
      };

      // Aggregate totals
      metrics.invocationsNexflow += totalAttempts;
      
      // Track latest p95
      if (row.p95_latency && (!metrics.p95LatencyNexflow || row.p95_latency > metrics.p95LatencyNexflow)) {
        metrics.p95LatencyNexflow = Math.round(row.p95_latency);
      }
    }

    // Calculate overall success rates
    for (const metrics of metricsMap.values()) {
      let totalSuccess = 0;
      let totalInvocations = 0;

      for (const agentMetrics of Object.values(metrics.byAgentType)) {
        if (agentMetrics) {
          totalInvocations += agentMetrics.invocations;
          totalSuccess += agentMetrics.invocations * agentMetrics.successRate;
        }
      }

      metrics.successRateNexflow = totalInvocations > 0 
        ? Math.round((totalSuccess / totalInvocations) * 1000) / 1000 
        : 0;
    }

    const metricsArray = Array.from(metricsMap.values());

    logger.info({
      timeframe,
      facilitatorCount: metricsArray.length,
      totalInvocations: metricsArray.reduce((sum, m) => sum + m.invocationsNexflow, 0),
      msg: 'NexFlow metrics aggregated',
    });

    return {
      ok: true,
      timeframe,
      metrics: metricsArray,
      timestamp: new Date().toISOString(),
    };
  } catch (error) {
    logger.error({
      error: error instanceof Error ? error.message : 'Unknown error',
      msg: 'Failed to get NexFlow metrics',
    });

    // Return empty metrics on error (graceful degradation)
    return {
      ok: true,
      timeframe,
      metrics: [],
      timestamp: new Date().toISOString(),
    };
  }
}

/**
 * Get NexFlow metrics summary for a specific facilitator
 */
export async function getNexflowMetricsForFacilitator(
  facilitatorId: string,
  timeframe: Timeframe = '1d'
): Promise<NexflowFacilitatorMetrics | null> {
  const response = await getNexflowMetrics({ facilitatorId, timeframe });
  return response.metrics[0] ?? null;
}

/**
 * Check if NexFlow has sufficient internal data for a facilitator
 */
export async function hasNexflowData(
  facilitatorId: string,
  minInvocations: number = 10
): Promise<boolean> {
  const metrics = await getNexflowMetricsForFacilitator(facilitatorId, '7d');
  return metrics !== null && metrics.invocationsNexflow >= minInvocations;
}

